import React, { useEffect, useState } from "react";
import classes from "./LMSAssignToMember.module.css";
import styled, { css, keyframes } from "styled-components";
import { BackBtn, CustomButton } from "../../exports";
import { Input, List, ListItem, ListItemText } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import parse from "html-react-parser";
import { motion } from "framer-motion";
import { useLead } from "../../../contexts/LMSDashboard/LeadContext";
import { rgba } from "polished";
import CheckIcon from "@material-ui/icons/Check";
import { v4 as uuid } from "uuid";
import { useLMSRules } from "../../../contexts/LMSRulesProvider";
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const Header = styled.header`
  padding-top: 7%;
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;

  button {
    width: 50px;
    padding: 1% 0%;
    justify-content: center;
    border-radius: 4px;
    height: unset;
  }
`;

const HeaderChild = styled.div`
  width: 60%;
  display: flex;
  justify-content: space-around;
  margin-top: 1%;

  h1 {
    margin-top: 1%;
    font-family: Roboto, sans sarif;
    font-weight: 500;
    line-height: 1.17;
    color: #24272c;
  }
`;

const skeltonColor = "#f3f3f3";
const borderRadius = "3px";

const Card = styled.div`
  border-radius: 0.5rem;
  background-color: transparent;
  color: white;
  width: 100%;
  padding: 1rem 0.5rem 1rem 0.5rem;
  margin: ${(p) => p.margin || 0};
`;

const glimmer = keyframes`
0% {
  transform:  rotate(10deg)
  translate(-200);
}
100% {
  transform: rotate(10deg) translate(200%, -50%)
}
`;
const skeltonShimmer = css`
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 200px;
  background: ${rgba("#190932", 0.05)};
  animation: ${glimmer} 1.25s infinite linear;
`;

const Skelton = styled.div`
  position: relative;
  overflow: hidden;
  width: ${(p) => p.width || "120px"};
  height: ${(p) => p.height || "12px"};
  margin: ${(p) => p.margin || 0};
  border-radius: ${borderRadius};
  background: ${(p) => p.background || skeltonColor};

  &::before {
    ${skeltonShimmer}
  }
`;

const Row = styled.div`
  display: flex;
  align-items: center;
`;

const FilterList = ({
  arrayToFilter,
  searchValue,
  handleSelect,
  selectedUser,
}) => {
  return arrayToFilter.map(({ name, department, team, userId }, i) => {
    const text = name;

    const matchStart = text
      .toLowerCase()
      .indexOf("" + searchValue.toLowerCase() + "");

    const matchEnd = matchStart + searchValue.length - 1;
    const beforeMatch = text.slice(0, matchStart);

    const matchText = text.slice(matchStart, matchEnd + 1);
    const afterMatch = text.slice(matchEnd + 1);

    const markedText = parse(`${beforeMatch}<b>${matchText}</b>${afterMatch}`);

    return (
      <ListItem
        onClick={() => handleSelect(userId)}
        className={classes.ListItem}
        key={i}
        button
      >
        <div>
          <ListItemText className={classes.ListItemText}>
            {markedText}
          </ListItemText>
          {department && (
            <ListItemText className={classes.ListItemText_Sub}>
              {team} {`${department && `-${department}`}`}
            </ListItemText>
          )}
        </div>
        {selectedUser === userId && <CheckIcon style={{ color: "#12B0EE" }} />}
      </ListItem>
    );
  });
};

const SkeltonLoader = () => {
  return (
    <Card margin="0 0 3% 0">
      <Row>
        <div style={{ marginLeft: "1rem", width: "100%" }}>
          <Skelton width="50%" margin="0 0 3% 0" />
          <Skelton width="80%" />
        </div>
      </Row>
    </Card>
  );
};

const LMSAssignToMemberPage = ({ businessId }) => {
  const { setSelectedUser, selectedUser, fetchAllUsers, members, setMembers } =
    useLead();

  const { openAssignPage, isOpenAssignPage, openTextFieldPage } = useLMSRules();

  const { setErrorMessage } = useToastMessage();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "-50", y: "100%", pointerEvents: "none" },
  };

  const [searchValue, setSearchValue] = useState("");

  const [filteredList, setFilteredList] = useState([]);

  const handleSearchValue = (e) => {
    setSearchValue(e.target.value);

    const filterValue = e.target.value.toLowerCase();

    const filtered = members.filter((d, i) => {
      const name = d.name.toLowerCase();

      return name.indexOf(filterValue) > -1;
    });

    return setFilteredList(filtered || []);
  };

  const handleSelect = (userId) => {
    return setSelectedUser(userId);
  };

  useEffect(() => {
    if (isOpenAssignPage && members.length < 1) {
      fetchAllUsers(businessId);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isOpenAssignPage]);

  const ShowLoader = () => {
    let d = [];
    for (let i = 0; i <= 3; i++) {
      d.push(SkeltonLoader());
    }

    return d;
  };

  const handleSubmit = () => {
    if (selectedUser) {
      setMembers([]);
      return openTextFieldPage(true);
    } else {
      return setErrorMessage("Please Select User!");
    }
  };

  return (
    <>
      <motion.section
        initial={{ opacity: 0, x: "0%", y: "-200%", pointerEvents: "none" }}
        transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
        animate={isOpenAssignPage ? "open" : "closed"}
        variants={variants}
        className={classes.Container}
      >
        <div className={classes.Header_Container}>
          <Header>
            <HeaderChild>
              <BackBtn setOpen={() => openAssignPage(false)} />
              <h1>Assign to member</h1>
            </HeaderChild>
            <CustomButton
              disabled={selectedUser ? false : true}
              onClick={handleSubmit}
            >
              Next
            </CustomButton>
          </Header>
          <div className={classes.Search_Field}>
            <div>
              <SearchIcon className={classes.Search_Icon} />
              <Input
                placeholder="Search team members"
                value={searchValue}
                onChange={handleSearchValue}
                className={classes.Search_Input_Node}
              />
            </div>
          </div>
        </div>

        <List
          className={classes.List}
          style={
            (isOpenAssignPage &&
              members.length > 0 && { overflowY: "scroll" }) || {
              overflowY: "hidden",
            }
          }
        >
          {(isOpenAssignPage && members.length > 0 && (
            <FilterList
              selectedUser={selectedUser}
              arrayToFilter={
                (filteredList.length > 0 && filteredList) || members
              }
              handleSelect={handleSelect}
              searchValue={searchValue}
            />
          )) || <ShowLoader key={uuid()} />}
        </List>
      </motion.section>
    </>
  );
};

export default LMSAssignToMemberPage;
