import { IconButton, Input } from "@material-ui/core";
import { motion } from "framer-motion";
import React from "react";
import { useLMSRules } from "../../../contexts/LMSRulesProvider";
import classes from "./LMSTextFieldPage.module.css";
import styled from "styled-components";
import { BackBtn, CustomButton } from "../../exports";
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const Header = styled.header`
  padding-top: 2%;
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;

  align-items: baseline;
`;

const HeaderChild = styled.div`
  width: 100%;
  display: grid;
  margin-top: 1%;
  grid-template-columns: 18% 82%;
  align-items: baseline;

  h1 {
    margin-top: 1%;
    font-family: Roboto, sans sarif;
    font-weight: 500;
    line-height: 1.17;
    color: #24272c;
  }

  button {
    height: min-content;
    margin-right: 5%;

    width: 45px;

    box-shadow: none;
  }
`;

const LMSTextFieldPage = ({ businessId }) => {
  const {
    isOpenTextFieldPage,
    openTextFieldPage,
    ruleParameters,
    newRuleData,
    setNewRuleData,
    createNewRule,
  } = useLMSRules();

  const { isDisabled } = useToastMessage();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "100%", y: "0%", pointerEvents: "none" },
  };

  const inputType =
    ruleParameters &&
    ruleParameters.find((e) => e.name === newRuleData.parameter);

  return (
    <motion.div
      initial={{ opacity: 0, x: "100%", y: "0%", pointerEvents: "none" }}
      transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
      animate={isOpenTextFieldPage ? "open" : "closed"}
      variants={variants}
      className={classes.Container}
    >
      <Header>
        <HeaderChild>
          <IconButton
            onClick={(e) => {
              openTextFieldPage(false);
            }}
          >
            <BackBtn
              close={() => {
                openTextFieldPage(false);
              }}
            />
          </IconButton>
          <h1>Enter Value</h1>
        </HeaderChild>
        <CustomButton
          disabled={isDisabled}
          className={classes.CustomButton}
          onClick={() => {
            createNewRule(businessId);
          }}
        >
          Done
        </CustomButton>
      </Header>
      <div className={classes.Search_Field}>
        <Input
          type={(inputType && inputType.type) || "text"}
          placeholder="Type Value"
          value={newRuleData.ruleValue || ""}
          onChange={(e) =>
            setNewRuleData({
              ...newRuleData,
              ruleValue: e.target.value,
            })
          }
          className={classes.Search_Input_Node}
        />
      </div>
    </motion.div>
  );
};

export default LMSTextFieldPage;
