import { useEffect, useRef } from "react";
import classes from "./LMSRules.module.css";
import { Modal2 } from "../../Components/exports";
import LMSRulesList from "../../Components/LMSRules/LMSRulesList/LMSRulesList";
import CreateRuleButton from "../../Components/LMSRules/CreateRuleButton/CreateRuleButton";
import LMSRulesContext, { useLMSRules } from "../../contexts/LMSRulesProvider";
import LMSRulesParameter from "../../Components/LMSRules/LMSRulesParameter/LMSRulesParameter";
import LMSRuleLogic from "../../Components/LMSRules/LMSRuleLogicPage/LMSRuleLogicPage";
import LMSAssignToMemberPage from "../../Components/LMSRules/LMSAssignToMemberPage/LMSAssignToMemberPage";
import LMSTextFieldPage from "../../Components/LMSRules/LMSTextFieldPage/LMSTextFieldPage";

const Rule = (props) => {
  const { getRules, rules, listConfig, getRuleCount } = useLMSRules();

  const fetchRef = useRef(() => {
    if (!rules) {
      getRules({ override: false, businessId: props.match.params.businessId });
    }

    getRuleCount(props.match.params.businessId);
  });

  useEffect(() => {
    fetchRef.current();
  }, []);

  return (
    <section className={classes.Rules_Page}>
      <Modal2 title={`${listConfig.ruleCount} Rules`} />
      <LMSRulesList businessId={props.match.params.businessId} />
      <CreateRuleButton businessId={props.match.params.businessId} />
      <LMSRulesParameter />
      <LMSRuleLogic />
      <LMSAssignToMemberPage businessId={props.match.params.businessIdF} />
      <LMSTextFieldPage businessId={props.match.params.businessId} />
    </section>
  );
};

const RuleContextProvider = (props) => {
  return (
    <LMSRulesContext>
      <Rule {...props} />
    </LMSRulesContext>
  );
};

export default RuleContextProvider;
