import React from "react";
import { Modal2 } from "../../exports";
import classes from "./LMSRulesParameter.module.css";
import { motion } from "framer-motion";
import { useLMSRules } from "../../../contexts/LMSRulesProvider";
import { List, ListItemText } from "@material-ui/core";
import { ListItem } from "@material-ui/core";
import { ListItemSecondaryAction } from "@material-ui/core";
import { Typography } from "@material-ui/core";

const LMSRulesParameter = () => {
  const {
    isOpenParameterPage,
    openRuleLogicPage,
    openParameterPage,
    ruleParameters,
    setNewRuleData,
  } = useLMSRules();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "-50", y: "100%", pointerEvents: "none" },
  };
  return (
    <motion.div
      initial={{ opacity: 0, x: "0%", y: "-200%", pointerEvents: "none" }}
      transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
      animate={isOpenParameterPage ? "open" : "closed"}
      variants={variants}
      className={classes.Container}
    >
      <Modal2
        close={() => openParameterPage(false)}
        title="Choose Rule Parameter"
      />

      <div className={classes.Parameter_Container}>
        <List>
          {ruleParameters &&
            ruleParameters.map((parameter, i) => {
              if (parameter.name === "date") {
                return null;
              }

              return (
                <ListItem
                  key={i}
                  className={classes.ListItem}
                  onClick={() => {
                    setNewRuleData((prevState) => ({
                      ...prevState,
                      parameter: parameter.name,
                      parameterType: parameter.type,
                    }));
                    // openParameterPage(false);
                    openRuleLogicPage(true);
                  }}
                  button
                >
                  <ListItemText
                    style={{ textTransform: "capitalize" }}
                    primary={parameter.name}
                  />
                  <ListItemSecondaryAction>
                    <Typography
                      className={classes.Typography}
                      variant="subtitle2"
                      component="p"
                    >
                      {parameter.count} Rules
                    </Typography>
                  </ListItemSecondaryAction>
                </ListItem>
              );
            })}
        </List>
      </div>
    </motion.div>
  );
};

export default LMSRulesParameter;
