import axios from "axios";
import React, { useContext, useState } from "react";
import { API } from "../Backend";
import {
  createNewRulePost,
  deleteRuleFetch,
  fetchRuleCount,
  fetchRuleParameters,
} from "../api/LMSRules";
import { useLoader } from "./LoaderContext";
import { useToastMessage } from "./ToastMessageContext";
import { useLead } from "./LMSDashboard/LeadContext";
import decryptResponse from "../Shared/decryptResponse";

export const LMSRules = React.createContext();

export const useLMSRules = () => useContext(LMSRules);

const LMSRulesProvider = ({ children }) => {
  const { startLoader, cancelLoader } = useLoader();

  const { selectedUser } = useLead();

  const [isOpenParameterPage, openParameterPage] = useState(false);
  const [isOpenRuleLogicPage, openRuleLogicPage] = useState(false);
  const [isOpenAssignPage, openAssignPage] = useState(false);
  const [isOpenTextFieldPage, openTextFieldPage] = useState(false);
  const [isLoadingAssignPage, startLoadingAssignPage] = useState(false);
  const [rules, setRules] = useState(null);

  const [ruleParameters, setRuleParameters] = useState(null);

  const [listConfig, setListConfig] = useState({
    hasMore: true,
    page: 1,
    loading: false,
    ruleCount: 0,
  });

  const [newRuleData, setNewRuleData] = useState({
    parameter: null,
    ruleValue: "",
    logic: "",
    parameterType: null,
  });

  const { setErrorMessage, setDisabled, setSuccessMessage } = useToastMessage();

  const getRules = async ({
    override = false,
    page = undefined,
    scroll = false,
    businessId = undefined,
  }) => {
    setListConfig({ ...listConfig, loading: true });

    if (!scroll) {
      startLoader();
    }

    axios({
      url: `${API}/get-rules?page=${page ? page : listConfig.page}`,
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
      },
    })
      .then((res) => {
        let data = decryptResponse({ data: res.data });
        if (rules && !override) {
          setRules([...rules, ...data.data]);
        } else {
          setRules(data.data);
        }

        setListConfig((prevState) => ({
          ...prevState,
          loading: false,
          page: page ? page : prevState.page + 1,
          hasMore: true,
        }));

        cancelLoader();
      })
      .catch((err) => {
        setListConfig((prevState) => ({
          ...prevState,
          loading: false,
          hasMore: false,
        }));

        setErrorMessage(err.message);

        cancelLoader();
      });
  };

  const getRuleCount = async (businessId) => {
    try {
      const res = await fetchRuleCount(businessId);

      if (res.error) {
        return setErrorMessage(res.error);
      }

      return setListConfig((prevState) => ({
        ...prevState,
        ruleCount: res.count,
      }));
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getRuleParameters = async (businessId) => {
    try {
      startLoader();
      const res = await fetchRuleParameters(businessId);

      if (res.error) {
        cancelLoader();
        return setErrorMessage(res.error);
      }

      cancelLoader();
      setRuleParameters(res.data);
    } catch (err) {
      cancelLoader();
      return setErrorMessage(err.message);
    }
  };

  const closeAllPages = () => {
    openAssignPage(false);
    openParameterPage(false);
    openRuleLogicPage(false);
    openTextFieldPage(false);

    setNewRuleData({
      parameter: null,
      ruleValue: "",
      logic: "",
      parameterType: null,
    });
  };

  const createNewRule = async (businessId) => {
    try {
      setDisabled();
      const res = await createNewRulePost(
        {
          assignUser: selectedUser,
          ruleValue: newRuleData.ruleValue,
          parameter: newRuleData.parameter,
          logic: newRuleData.logic,
          parameterType: newRuleData.parameterType || "string",
        },
        businessId
      );

      if (res.error) {
        return setErrorMessage(res.error);
      }

      setSuccessMessage(res.message);
      closeAllPages();
      getRules({ override: true, page: 1 });
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const deleteRule = async (ruleId, businessId) => {
    try {
      startLoader();
      const res = await deleteRuleFetch(ruleId, businessId);

      if (res.error) {
        cancelLoader();
        return setErrorMessage(res.error);
      }

      cancelLoader();

      getRules({ override: true, page: 1 });

      return setSuccessMessage(res.message);
    } catch (err) {
      cancelLoader();
      setErrorMessage(err.message);
    }
  };

  const value = {
    isOpenParameterPage,
    openParameterPage,
    isOpenRuleLogicPage,
    openRuleLogicPage,
    isOpenAssignPage,
    openAssignPage,
    isLoadingAssignPage,
    startLoadingAssignPage,
    deleteRule,
    getRules,
    rules,
    listConfig,
    setListConfig,
    getRuleCount,
    getRuleParameters,
    ruleParameters,
    newRuleData,
    setNewRuleData,
    isOpenTextFieldPage,
    openTextFieldPage,
    createNewRule,
  };

  return <LMSRules.Provider value={value}>{children}</LMSRules.Provider>;
};

export default LMSRulesProvider;
