import React from "react";
import { Modal2 } from "../../exports";
import classes from "./LMSRuleLogicPage.module.css";
import { motion } from "framer-motion";
import { useLMSRules } from "../../../contexts/LMSRulesProvider";
import { List, ListItemText } from "@material-ui/core";
import { ListItem } from "@material-ui/core";

const LMSRuleLogicPage = () => {
  const {
    isOpenRuleLogicPage,
    openRuleLogicPage,
    openAssignPage,
    setNewRuleData,
  } = useLMSRules();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "100%", y: "0%", pointerEvents: "none" },
  };
  return (
    <motion.div
      initial={{ opacity: 0, x: "100%", y: "0%", pointerEvents: "none" }}
      transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
      animate={isOpenRuleLogicPage ? "open" : "closed"}
      variants={variants}
      className={classes.Container}
    >
      <Modal2 close={() => openRuleLogicPage(false)} title="Rule Logic" />

      <div className={classes.Parameter_Container}>
        <List>
          <ListItem
            className={classes.ListItem}
            onClick={() => {
              setNewRuleData((prevState) => ({ ...prevState, logic: "match" }));

              openAssignPage(true);
            }}
            button
          >
            <ListItemText primary="Exact Match" />
          </ListItem>
        </List>
      </div>
    </motion.div>
  );
};

export default LMSRuleLogicPage;
