import {
  IconButton,
  ListItemSecondaryAction,
  ListItemText,
  Menu,
  MenuItem,
} from "@material-ui/core";
import { List, ListItem } from "@material-ui/core";
import React, { useCallback, useState } from "react";
import classes from "./LMSRulesList.module.css";
import clsx from "clsx";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { useLMSRules } from "../../../contexts/LMSRulesProvider";
import { useRef } from "react";

const LMSRulesList = ({ businessId }) => {
  const { rules, listConfig, getRules, deleteRule } = useLMSRules();

  const [ruleId, setRuleId] = useState(null);

  const observer = useRef();

  const { loading, hasMore } = listConfig;

  const lastElementRef = useCallback(
    (node) => {
      if (loading) return;
      if (observer.current) observer.current.disconnect();

      observer.current = new IntersectionObserver(async (entries) => {
        if (entries[0].isIntersecting && hasMore && !loading) {
          return await getRules({ scroll: true, businessId });
        }
      });
      if (node) observer.current.observe(node);
    },
    [loading, hasMore]
  );

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={clsx(classes.Container)}>
      <List className={clsx(classes.List)}>
        {rules &&
          rules.map((rule, i) => {
            return (
              <ListItem
                ref={lastElementRef || null}
                classes={{ root: classes.ListItem }}
                key={i}
              >
                <ListItemText
                  classes={{
                    primary: classes.PrimaryText,
                    secondary: classes.SecondaryText,
                  }}
                  primary={rule.name}
                  secondary={
                    <>
                      {`${rule.param_name} ${rule.logic} - `}
                      <span>{rule.value}</span>
                    </>
                  }
                />
                <ListItemSecondaryAction style={{ paddingRight: 0 }}>
                  <IconButton
                    onClick={(e) => {
                      setRuleId(rule.ruleId);
                      handleClick(e);
                    }}
                    classes={{ root: classes.IconButton }}
                  >
                    <MoreVertIcon />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            );
          })}
      </List>
      <Menu
        getContentAnchorEl={null}
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
      >
        <MenuItem
          style={{ height: "min-content" }}
          onClick={() => {
            handleClose();
            deleteRule(ruleId, businessId);
          }}
        >
          Delete
        </MenuItem>
      </Menu>
    </div>
  );
};

export default LMSRulesList;
