import clsx from "clsx";
import React from "react";
import { useLMSRules } from "../../../contexts/LMSRulesProvider";
import { CustomButton } from "../../exports";
import classes from "./CreateRuleButton.module.css";

const CreateRuleButton = ({ businessId }) => {
  const { openParameterPage, getRuleParameters, ruleParameters } =
    useLMSRules();

  return (
    <div className={clsx(classes.Container, "col-10")}>
      <CustomButton
        className={classes.Button}
        onClick={async () => {
          if (!ruleParameters) {
            await getRuleParameters(businessId);
          }

          return openParameterPage(true);
        }}
      >
        Create New Rule
      </CustomButton>
    </div>
  );
};

export default CreateRuleButton;
